@import url("https://fonts.googleapis.com/css?family=Lato:300,400,700,900");
@import url("https://fonts.googleapis.com/css?family=Lato:300,400,700,900");
/** Media Queries **/
@media (min-width: 768px) {
  .contenedor-anuncios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.contenedor-anuncios .anuncio {
  background-color: #eeeeee;
  border: 1px solid #c8c8c8;
  margin-bottom: 3rem;
  padding: 2rem;
}
.contenedor-anuncios .anuncio .contenido-anuncio {
  padding: 2rem;
}
.contenedor-anuncios .anuncio h3, .contenedor-anuncios .anuncio p {
  margin: 0;
}

.precio {
  color: #71B100;
  font-weight: 700;
  font-size: 3rem;
}

.iconos-caracteristicas {
  display: flex;
  justify-content: space-evenly;
  padding: 0;
  max-width: 40rem;
}
.iconos-caracteristicas li {
  flex: 1;
  display: flex;
}
.iconos-caracteristicas li img {
  flex: 0 0 3rem;
  margin-right: 1rem;
}
.iconos-caracteristicas li p {
  font-weight: 900;
}

@media (min-width: 768px) {
  .contenedor-blog {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}
.entrada-blog {
  margin-bottom: 2rem;
}
.entrada-blog:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .entrada-blog {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
  }
}

.texto-blog a {
  color: #000000;
  display: block;
  margin-top: 3rem;
}
@media (min-width: 768px) {
  .texto-blog a {
    margin-top: 0;
  }
}
.texto-blog h4 {
  margin: 0;
  line-height: 1.4;
}
.texto-blog h4::after {
  content: " ";
  display: block;
  width: 15rem;
  height: 0.5rem;
  background-color: #71B100;
  margin-top: 1rem;
}
.texto-blog span {
  color: #E08709;
}

.formulario p {
  font-size: 1.4rem;
  color: #333333;
  margin: 2rem 0 0;
}
.formulario legend {
  font-size: 2rem;
  text-transform: uppercase;
}
.formulario label {
  display: block;
  font-weight: 700;
  text-transform: uppercase;
}
.formulario input:not([type=submit]), .formulario textarea, .formulario select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #FFFFFF;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
  margin-bottom: 1.6rem;
}
.formulario textarea {
  height: 20rem;
}
.formulario .forma-contacto {
  display: flex;
  max-width: 30rem;
  justify-content: space-between;
  align-items: center;
}
.formulario input[type=radio] {
  margin: 0;
  width: auto;
}

@media (min-width: 768px) {
  .contenido-nosotros {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.texto-nosotros blockquote {
  font-weight: 900;
  font-size: 2.2rem;
  margin: 0;
  padding: 1rem 0 3rem 0;
}

.alerta {
  text-transform: uppercase;
  font-weight: bold;
  padding: 1rem 0;
  text-align: center;
  color: white;
  display: none;
}
.alerta.error {
  background-color: red;
  display: block;
}
.alerta.correcto {
  background-color: #71B100;
  display: block;
}
.alerta.fallido {
  background-color: red;
  display: block;
}

table.propiedades {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0;
}
table.propiedades thead {
  background-color: #71B100;
}
table.propiedades thead th {
  color: #FFFFFF;
  padding: 2rem;
}
table.propiedades .img-tabla {
  width: 10rem;
}

tbody tr td {
  color: #000000;
}

.img-propiedad {
  width: 50%;
  height: 25rem;
}