@use "../base/mixins.scss" as m;
@use  "../base/variables.scss" as v;


.contenedor-anuncios {
    @include m.tablet {
        @include m.grid(3, 2rem)
    }



    .anuncio {
        background-color: lighten(v.$gris, 5);
        border: 1px solid darken(v.$gris, 10);
        margin-bottom: 3rem;
        padding: 2rem;

        .contenido-anuncio {
            padding: 2rem;     
        }

        h3, p {
            margin: 0;
        }
    }
}
.precio {
    color: v.$verde;
    font-weight: v.$bold;
    font-size: 3rem;
}

.iconos-caracteristicas {
    display: flex;
    justify-content: space-evenly;
    padding: 0;
    max-width: 40rem;

    li {
        flex: 1;
        display: flex;

        img {
            flex: 0 0 3rem;
            margin-right: 1rem;
        }

        p {
            font-weight: v.$black;
        }
    }
}